          <div class="wrapper" @click="ncshow = true">
            <div class="nc">
              <div class="ncipt">
                <input
                  type="text"
                  v-model="name"
                  @click="ncipt"
                  style="color: black"
                />
              </div>
              <button class="ncbtn" @click="ncbtn">确认修改</button>
              <button class="ncbtn2" @click="ncbtnc">取消</button>
            </div>
          </div>
        </van-overlay>
      </div>
      <div class="operation-three">
        <div class="three-text">性别</div>
        <dir>{{ sex }}</dir>
