</template>

<script>
import {nanoid} from "nanoid";
import { Button, Overlay, Toast, DatetimePicker, Uploader } from "vant";
import axios from "axios";
export default {
  data() {
    return {
      txshow: false,
      ncshow: false,
      xbshow: false,
      srshow: false,
      minDate: new Date(1990, 0, 1),
      maxDate: new Date(2022, 10, 22),
      currentDate: localStorage.getItem("birthday"),
      name: localStorage.getItem("nickname"),
      birthday: localStorage.getItem("birthday"),
      sex: localStorage.getItem("sex"),
      man: "男",
      woman: "女",
      avater: localStorage.getItem("avatar"),
      img1: [],
      files:null
    };
  },
  components: {
    [Button.name]: Button,
    [Overlay.name]: Overlay,
    [DatetimePicker.name]: DatetimePicker,
    [Uploader.name]: Uploader,
  },
  methods: {
    afterRead(file) {
      // 此时可以自行将文件上传至服务器
      // console.log(file);
      this.img1 = file.content;
      this.files = file.file
    },
    back() {
      this.$router.push("/my");
    },
    ncipt() {
      event.stopPropagation();
    },
    ncbtn() {
      // alert(this.name)
      // console.log(this.name);
      // event.stopPropagation()
    },
    ncbtnc() {
      this.ncshow = false;
    },
    xbipt() {
      event.stopPropagation();
    },
    xbbtn() {
      // this.sex = ''
      // event.stopPropagation()
    },
    xbbtnc() {
      this.xbshow = false;
    },
    sript() {
      event.stopPropagation();
    },
    srbtn() {
      // this.birthday = ''
      // birthday = currentDate
      event.stopPropagation();
    },
    qd() {
      this.birthday = this.currentDate.toLocaleDateString();
    },
    change() {
      axios({
        method: "get",
        url: "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/upload/token",
      }).then((res) => {
        // console.log(res);
        let fd = new FormData();
        fd.append("token", res.data.token);
        fd.append("file", this.files);
        fd.append("key", nanoid());
      axios({
        method: "post",
        url: "https://upload-z1.qiniup.com",
        data: fd,
      }).then((res) => {
        console.log("http://toutiao.longxiaokj.com/" + res.data.key);
        axios({
          method: "post",
          url: "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/editUserInfo",
          data: {
            uid: localStorage.getItem("uid"),
            avatar: "http://toutiao.longxiaokj.com/" + res.data.key,
            nickname: this.name,
            birthday: this.birthday,
            sex: this.sex,
          },
        }).then((res) => {
          console.log(res);
          if (res.data.msg == "更新成功") {
            Toast("更改成功");
          } else {
            Toast("更改失败");
          }
        });
      });
    });
    },
  },
};
</script>

